import React from "react"
import { graphql, useStaticQuery } from 'gatsby'
import BackgroundImage from 'gatsby-background-image'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize } from '../../util/style_property'

const KeyVisual = (isTop = false) => {
  const data = useStaticQuery(
    graphql`
      query{
        site{
          siteMetadata{
            title
            subtitle
          }
        }
        file(name: {eq: "key_visual"}){
          childImageSharp{
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )

  const imageData = data.file.childImageSharp.fluid
  const siteMetadata = data.site.siteMetadata
  const siteTitle = isTop ? <SiteTitle>{siteMetadata.title}</SiteTitle> : <SiteTitleH2>{siteMetadata.title}</SiteTitleH2>
  return (
    <Wrapper
      Tag="div"
      fluid={imageData}>
      <div>
        {siteTitle}
        <SiteSubtitle>{siteMetadata.subtitle}</SiteSubtitle>
      </div>
    </Wrapper>
  )
}

const Wrapper = styled(BackgroundImage)`
  display: flex;
  align-items: center;
  width: 100%;
  height: 60vh;
  padding-left: 20%;
  background-position: bottom right;
  background-size: contain;
  background-color: ${color.green};
`
const SiteTitle = styled.h1`
  padding: 0;
  margin: 0;
  font-size: ${textSize.h1};
  color: ${color.text_black};
`

const SiteTitleH2 = SiteTitle.withComponent('h2')

const SiteSubtitle = styled.p`
  padding: 0;
  margin: 0;
  font-size: ${textSize.l};
  color: ${color.text_black};
`

export default KeyVisual