import React from "react"
import styled from 'styled-components'
import { padding } from '../../util/style_property'

const ColumnOf2 = ({ main, aside }) => {

  return (
    <Wrapper>
      <MainContents>{main}</MainContents>
      <SideContents>{aside}</SideContents>
    </Wrapper>
  )
}

const Wrapper = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  padding: ${padding.l};
  margin: 0;
  justify-content: space-around;
`

const MainContents = styled.main`
  display: block;
  width: 64%; 
`

const SideContents = styled.aside`
  display: block;
  width: 30%; 
`

export default ColumnOf2
